<template><div><h1>异步加载组件</h1>
<p>将所有页面组件一次性加载是一个很浪费资源和考验用户耐心的做法，尤其在移动端。</p>
<h2>使用</h2>
<p><code>webpack</code> 提供了<a href="https://webpack.js.org/guides/code-splitting-require/">code splitting</a>，你可以按照下面写法实现当切换到特定路由时才加载代码。</p>
<pre class="hljs"><code>{
  <span class="hljs-attr">path</span>: <span class="hljs-string">'/somepath'</span>,
  <span class="hljs-attr">component</span>: <span class="hljs-function"><span class="hljs-keyword">function</span> (<span class="hljs-params">resolve</span>) </span>{
    <span class="hljs-built_in">require</span>([<span class="hljs-string">'./demos/somepath.vue'</span>], resolve)
  }
}
</code></pre>
<h2>错误处理</h2>
<p>在一些情况下可能会加载失败，需要提示用户。</p>
<ul class="contains-task-list">
<li class="task-list-item"><input class="task-list-item-checkbox" disabled="" type="checkbox"> todo</li>
</ul>
</div></template><script>
export default {
  head: {"title":" Vue 异步加载组件"}
}
  </script>


